<template>
<div class="Extraction-record">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria" style="line-height: 5;">
      <Col :xs="12" :sm="12" :lg="4">
        <p>{{$t("common.Username")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Cash_result")}}&nbsp</p>
        <Select style="width:150px;margin-right:10px;">
            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col :xs="12" :sm="12" :lg="9">
        <p>{{$t("common.Cash_time")}}&nbsp</p>
        <DatePicker type="date" placeholder="" style="width: 110px"></DatePicker> -
        <DatePicker type="date" placeholder="" style="width: 110px"></DatePicker>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="center" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'Extraction_record',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            cityList: [
                {
                    value: 'All',
                    label: this.$t("common.All")
                },
                {
                    value: 'Yes',
                    label: this.$t("common.Withdrawal_success")
                },
                {
                    value: 'No',
                    label: this.$t("common.Pending_review")
                }
            ],
            columns1: [
              {
                  title: this.$t("common.Trading_flow"),
                  key: 'trading_flow',
                  align:'center'
              },
              {
                  title: this.$t("common.Username"),
                  key: 'username',
                  align:'center'
              },
              {
                  title: this.$t("common.realname"),
                  key: 'real_name',
                  align:'center'
              },
              {
                  title: this.$t("common.Withdrawal_Amount"),
                  key: 'amount',
                  align:'center'
              },
              {
                  title: this.$t("common.Withdrawal_status"),
                  key: 'state',
                  align:'center'
              },
              {
                  title: this.$t("common.Cash_time"),
                  key: 'time',
                  align:'center'
              },
              {
                  title: this.$t("common.Operate"),
                  key: 'operation',
                  align:'center',
                  width:120
              }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null
        }
    },
    methods:{
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              data.push({
                  username: Math.random().toString(36).substr(7),
                  real_name: parseInt(Math.random()*(3-1)+0)>0?'黄圣依':'杨幂',
                  trading_flow: '2018031259001024'+i,
                  time:this.formatDate(this.getRandomDateBetween()),
                  amount:parseInt(Math.random()*(11-1)+1)+',000.00',
                  state:parseInt(Math.random()*(3-1)+0)>0?'提现成功':'待审核',
                  operation:parseInt(Math.random()*(3-1)+0)>0?'审核':'完成'
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      formatTime (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          return y + '-' + m + '-' + d;
      },
      getMoble() {
          var prefixArray = new Array("130", "131", "132", "133", "135", "137", "138", "170", "187", "189");
          var i = parseInt(10 * Math.random());
          var prefix = prefixArray[i];

          for (var j = 0; j < 8; j++) {
              prefix = prefix + Math.floor(Math.random() * 10);
          }
          return prefix
      },
      onedata (data,index){
        //console.log(data,index)
        this.Personal_info = data
        //this.sendParams ()
      },
      sendParams () {
          this.$router.push({
              path: '/Userinfo',
              name: 'Userinfo',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       }
    },
    created(){
         this.handleListApproveHistory();
    }
}
</script>

<style lang="css">
.Extraction-record{
  
}
.Extraction-record .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.Extraction-record .search_criteria p{
  height: 30px;
}
.Extraction-record .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  background-color: #fff;
  text-align: center;
}
</style>



